import React from "react";
import Typical from "react-typical";
import About from "./About";
import Projects from "./Projects";
const Home = () => {
  return (
    <>
      <section className="container my-5 py-4">
        <div className="row">
          <div className="col-md-5  mt-5">
            <h1 className="home_title display-5 mb-4">
              Hi,
              <br />
              I'am <span className="home_title-color">Bhavesh</span>
            </h1>
            <Typical
              steps={[
                "Front End Developer",
                1000,
                "React Developer",
                1000,
                "Passionate Learner",
                1000,
              ]}
              loop={Infinity}
              wrapper="p"
              className="font-size"
            />

            <a href="#" className="button m-4">
              Contact Me
            </a>
            <a
              href="Bhavesh_Wadhwa_SVVV_Resume.pdf"
              className="button"
              download
            >
              Download Resume
            </a>
          </div>
          <div className="col-md-7 d-flex justify-content-center align-items-center">
            <img src="perfil.png" alt="profile" />
          </div>
          <div className="home_social">
            <a
              href="https://www.facebook.com/bhavesh.wadhwa2"
              className="home_social-icon"
            >
              <i class="bx bxl-facebook"></i>
            </a>
            <a
              href="https://www.linkedin.com/in/bhavesh-wadhwa-3806a6189/"
              className="home_social-icon"
            >
              <i class="bx bxl-linkedin"></i>
            </a>
            <a href="" className="home_social-icon">
              <i class="bx bxl-github"></i>
            </a>
            <a
              href="https://www.instagram.com/bhavesh_wadhwa6599/"
              className="home_social-icon"
            >
              <i class="bx bxl-instagram"></i>
            </a>
            <a href="#" className="home_social-icon">
              <i class="bx bxl-google"></i>
            </a>
          </div>
        </div>
      </section>
      <About />
      <Projects />
    </>
  );
};

export default Home;
